import { Doughnut, mixins } from "vue-chartjs";
const { reactiveProp } = mixins;

export default {
  extends: Doughnut,
  mixins: [reactiveProp],
  data: () => ({
    options: {
      responsive: true,
      maintainAspectRatio: false,
      layout: {
        padding: {
          left: 10,
          right: 10,
          top: 5,
          bottom: 10
        }
      },
      legend: {
        position: "bottom"
      },
      cutoutPercentage: 60
    }
  }),

  mounted() {
    this.renderChart(this.chartData, this.options);
  }
};
