<template>
  <div>
    <div class="row">
      <div class="col-md-6">
        <div class="row">
          <div class="col chart-bg m-3 overflow-auto">
            <div class="text-center">
              <div class="key-number">
                {{ processData.length }}
              </div>
              <h6>
                Processes Assessed
              </h6>
            </div>
          </div>
          <div class="col chart-bg m-3 overflow-auto">
            <div class="text-center">
              <div class="key-number">{{ savings }}</div>
              <h6>
                Total Estimated Savings
              </h6>
            </div>
          </div>
        </div>
      </div>
      <div class="col-md-6">
        <div class="row">
          <div class="col chart-bg m-3 overflow-auto">
            <div class="text-center">
              <div class="key-number">{{ hours }}</div>
              <h6>
                Annual Hours Saved
              </h6>
            </div>
          </div>
          <div class="col chart-bg m-3 overflow-auto">
            <div class="text-center">
              <div class="key-number">{{ productivity }} : 1</div>
              <h6>
                Hours Saved : Week Effort
              </h6>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FourHighlights",
  props: ["processData"],
  computed: {
    savings() {
      if (this.processData.length == 0) {
        return "0";
      } else {
        var totalSavings = this.processData.reduce((a, b) => ({
          savings: a.savings + b.savings
        }));

        var savingsOutput = "";

        if (totalSavings.savings < 999) {
          savingsOutput = "$" + totalSavings.savings + "k";
        } else {
          var millions = Math.round((100 * totalSavings.savings) / 1000) / 100;
          savingsOutput = "$" + millions + "M";
        }
        return savingsOutput;
      }
    },
    hours() {
      if (this.processData.length == 0) {
        return 0;
      } else {
        var totalHours = this.processData.reduce((a, b) => ({
          hours: a.hours + b.hours
        }));
        return totalHours.hours;
      }
    },
    productivity() {
      if (this.processData.length == 0) {
        return 0;
      } else {
        var totalHours = this.processData.reduce((a, b) => ({
          hours: a.hours + b.hours
        }));
        var totalEffort = this.processData.reduce((a, b) => ({
          effort: a.effort + b.effort
        }));
        return Math.round(totalHours.hours / totalEffort.effort);
      }
    }
  },
  data() {
    return {};
  }
};
</script>

<style scoped>
.chart-bg {
  background-color: white;
  filter: drop-shadow(-3px 1px 4px grey);
}

.key-number {
  font-size: 2.5em;
}
</style>
