<template>
  <div>
    <b-row class="mt-4">
      <b-col class="col-4"> </b-col>
      <b-col class="col-4">
        <div align="center">
          <h4 class="mb-4">Upload or Download Table Data</h4>
          <div align="left">
            <b-form-file
              size="sm"
              v-model="file"
              placeholder="Choose existing CSV file..."
            ></b-form-file>
          </div>

          <b-button
            size="sm"
            class="mt-3"
            variant="secondary"
            @click="$emit('upload-csv', file)"
            >Upload CSV to Table</b-button
          >
          <h6 class="my-3">Or</h6>
          <b-button size="sm" variant="secondary" @click="$emit('download-csv')"
            >Download Table to CSV</b-button
          >
        </div>
      </b-col>
      <b-col class="col-4"> </b-col>
    </b-row>
  </div>
</template>

<script>
export default {
  name: "UserDataStorage",
  data() {
    return {
      file: null
    };
  }
};
</script>

