import { Scatter, mixins } from "vue-chartjs";
const { reactiveProp } = mixins;

export default {
  extends: Scatter,
  mixins: [reactiveProp],
  data: () => ({
    options: {
      responsive: true,
      maintainAspectRatio: false,
      legend: {
        position: "bottom"
      },
      layout: {
        padding: {
          left: 20,
          right: 20,
          top: 20,
          bottom: 50
        }
      },
      tooltips: {
        callbacks: {
          label: function(tooltipItem, data) {
            return (
              data.datasets[tooltipItem.datasetIndex].data[tooltipItem.index]
                .label +
              ": (" +
              tooltipItem.xLabel +
              " hrs, $" +
              tooltipItem.yLabel +
              "k)"
            );
          }
        }
      },
      scales: {
        xAxes: [
          {
            scaleLabel: {
              display: true,
              labelString: "Estimated Development Effort (Weeks)"
            },
            ticks: {
              suggestedMin: 0,
              suggestedMax: 15
            }
          }
        ],
        yAxes: [
          {
            scaleLabel: {
              display: true,
              labelString: "Potential Savings (Thousands $)"
            },
            ticks: {
              suggestedMin: 0,
              suggestedMax: 1000
            }
          }
        ]
      }
    }
  }),
  mounted() {
    this.renderChart(this.chartData, this.options);
  }
};
