<template>
  <div class="py-3">
    <b-row>
      <b-col class="my-2">
        <b-form-group label="Filter" label-for="filter-input" class="mb-0">
          <b-input-group size="sm">
            <b-form-input
              id="filter-input"
              v-model="filter"
              type="search"
              placeholder="Type to Search"
            ></b-form-input>

            <b-input-group-append>
              <b-button :disabled="!filter" @click="filter = ''"
                >Clear</b-button
              >
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>
      <b-col class="mt-3 pt-4">
        <b-button size="sm" variant="secondary" @click="addProcess"
          >Add Process</b-button
        >
      </b-col>
      <b-col>
        <b-form-group
          inline
          label="Per page"
          label-for="per-page-select"
          label-size="sm"
          label-align
          class="m-0 col-lg-4 col-md-5 col-sm-5 ml-auto"
        >
          <b-form-select
            id="per-page-select"
            v-model="perPage"
            :options="pageOptions"
            size="sm"
          ></b-form-select>
        </b-form-group>
      </b-col>
    </b-row>

    <!-- Main table element -->
    <b-table
      :items="items"
      :fields="fields"
      :current-page="currentPage"
      :per-page="perPage"
      :filter="filter"
      :filter-included-fields="filterOn"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
      :sort-direction="sortDirection"
      stacked="md"
      show-empty
      small
      @filtered="onFiltered"
      hover
      striped
    >
      <template #cell(savings)="row"> ${{ row.value }}k </template>
      <template #cell(effort)="row"> {{ row.value }} weeks </template>

      <template #cell(modify)="row">
        <b-icon
          icon="pencil"
          variant="secondary"
          scale="1.25"
          class="mx-2 pointer"
          @click="info(row.item, row.index, $event.target)"
        ></b-icon>
        <b-icon
          icon="trash"
          variant="secondary"
          scale="1.25"
          class="mx-2 pointer"
          @click="deleteProcess(row.item.id)"
        ></b-icon>
      </template>
    </b-table>

    <!-- Info modal -->
    <b-modal
      :id="infoModal.id"
      :title="infoModal.title"
      ok-only
      @ok="handleOk"
      @hidden="resetInfoModal"
    >
      <form ref="form" @submit.stop.prevent="handleSubmit">
        <b-form-group
          label="Process Name"
          label-for="name-input"
          invalid-feedback="Name is required"
          :state="processNameState"
        >
          <b-form-input
            id="name-input"
            v-model="processName"
            :state="processNameState"
            required
          ></b-form-input>
        </b-form-group>
        <b-form-group
          label="Recommendation"
          label-for="recommendation-input"
          :state="recommendationState"
          invalid-feedback="Recommnedation is required"
        >
          <b-form-select
            id="recommendation-input"
            v-model="form.recommendation"
            :options="recommendations"
            required
          ></b-form-select>
        </b-form-group>
        <b-form-group
          label="Opportunity"
          label-for="opportunity-input"
          :state="opportunityState"
          invalid-feedback="Opportunity is required"
        >
          <b-form-select
            id="opportunity-input"
            v-model="form.opportunity"
            :options="opportunities"
            required
          ></b-form-select>
        </b-form-group>
        <b-form-group
          label="Savings"
          label-for="savings-input"
          invalid-feedback="Invalid savings value"
          :state="savingsState"
        >
          <b-form-input
            id="savings-input"
            v-model="savings"
            :state="savingsState"
            required
          ></b-form-input>
        </b-form-group>
        <b-form-group
          label="Hours Saved"
          label-for="hours-input"
          invalid-feedback="Invalid hours saved value"
          :state="hoursState"
        >
          <b-form-input
            id="hours-input"
            v-model="hours"
            :state="hoursState"
            required
          ></b-form-input>
        </b-form-group>
        <b-form-group
          label="Est Dev Effort"
          label-for="effort-input"
          invalid-feedback="Invalid dev effort value"
          :state="effortState"
        >
          <b-form-input
            id="effort-input"
            v-model="effort"
            :state="effortState"
            required
          ></b-form-input>
        </b-form-group>
      </form>
    </b-modal>
    <b-row>
      <b-col class="col-3"></b-col>
      <b-col class="col-6">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          align="fill"
          size="sm"
          class="my-0"
        ></b-pagination>
      </b-col>
      <b-col class="col-3"> </b-col>
    </b-row>
    <UserDataStorage
      v-on:upload-csv="uploadCSV"
      v-on:download-csv="$emit('download-csv')"
    />
  </div>
</template>

<script>
import UserDataStorage from "./UserDataStorage";
export default {
  name: "TableData",
  components: { UserDataStorage },
  props: ["items"],
  data() {
    return {
      file: "",
      fields: [
        {
          key: "name",
          label: "Process Name",
          sortable: true
        },
        { key: "recommendation", label: "Recommendation", sortable: true },
        { key: "opportunity", label: "Opportunity", sortable: false },
        {
          key: "savings",
          label: "Savings",
          sortable: false,
          class: "text-center"
        },
        { key: "hours", label: "Hours Saved", sortable: false },
        { key: "effort", label: "Est Dev Effort", sortable: false },
        { key: "modify", label: "", class: "text-right" }
      ],
      totalRows: 1,
      currentPage: 1,
      perPage: 10,
      pageOptions: [10, 20, 30],
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      filterOn: [],
      infoModal: {
        id: "info-modal",
        title: "",
        content: ""
      },
      currentRowContent: null,
      processName: "",
      processNameState: null,
      recommendationState: null,
      opportunityState: null,
      form: {
        recommendation: null,
        opportunity: null
      },
      recommendations: [
        { text: "-Select Recommendation-", value: null },
        "Recommended",
        "Has Potential",
        "Not Recommended"
      ],
      opportunities: [
        { text: "-Select Opportunity-", value: null },
        "Front Office",
        "Back Office"
      ],
      savings: "",
      savingsState: null,
      hours: "",
      hoursState: null,
      effort: "",
      effortState: null
    };
  },

  watch: {
    items() {
      this.totalRows = this.items.length;
    }
  },

  mounted() {
    // Set the initial number of items
    this.totalRows = this.items.length;
  },
  methods: {
    uploadCSV(file) {
      this.$emit("upload-csv", file);
    },
    info(item, index, button) {
      this.infoModal.title = "Edit Process Item";
      this.currentRowContent = item;
      this.processName = item.name;
      this.form.recommendation = item.recommendation;
      this.form.opportunity = item.opportunity;
      this.savings = item.savings;
      this.hours = item.hours;
      this.effort = item.effort;
      this.$root.$emit("bv::show::modal", this.infoModal.id, button);
    },
    resetInfoModal() {
      this.infoModal.title = "";
      this.infoModal.content = "";
      this.processName = "";
      this.processNameState = null;
      this.form.recommendation = null;
      this.recommendationState = null;
      this.form.opportunity = null;
      this.opportunityState = null;
      this.savings = "";
      this.savingsState = null;
      this.hours = "";
      this.hoursState = null;
      this.effort = "";
      this.effortState = null;
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault();
      // Trigger submit handler
      this.handleSubmit();
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.checkFormValidity()) {
        return;
      }
      // Push the name to submitted names
      if (this.currentRowContent !== null) {
        this.$emit("edit-process", {
          id: this.currentRowContent.id,
          name: this.processName,
          recommendation: this.form.recommendation,
          opportunity: this.form.opportunity,
          savings: parseInt(this.savings),
          hours: parseInt(this.hours),
          effort: parseInt(this.effort)
        });
        this.currentRowContent = null;
      } else {
        this.addItems();
      }

      // Hide the modal manually
      this.$nextTick(() => {
        this.$bvModal.hide("info-modal");
      });
    },
    checkFormValidity() {
      if (this.processName !== "") {
        this.processNameState = true;
      } else {
        this.processNameState = false;
      }
      if (this.form.recommendation !== null) {
        this.recommendationState = true;
      } else {
        this.recommendationState = false;
      }
      if (this.form.opportunity !== null) {
        this.opportunityState = true;
      } else {
        this.opportunityState = false;
      }
      if ((this.savings !== "") & !isNaN(this.savings)) {
        this.savingsState = true;
      } else {
        this.savingsState = false;
      }
      if ((this.hours !== "") & !isNaN(this.hours)) {
        this.hoursState = true;
      } else {
        this.hoursState = false;
      }
      if ((this.effort !== "") & !isNaN(this.effort)) {
        this.effortState = true;
      } else {
        this.effortState = false;
      }

      return (
        this.processNameState &
        this.recommendationState &
        this.opportunityState &
        this.savingsState &
        this.hoursState &
        this.effortState
      );
    },
    addItems() {
      var timestamp = new Date();
      var uuid =
        "" +
        timestamp.getMinutes() +
        timestamp.getSeconds() +
        timestamp.getMilliseconds();

      this.$emit("add-process", {
        id: uuid,
        name: this.processName,
        recommendation: this.form.recommendation,
        opportunity: this.form.opportunity,
        savings: this.savings,
        hours: this.hours,
        effort: this.effort
      });
    },

    addProcess() {
      this.showInfoModal();
    },
    showInfoModal() {
      this.infoModal.title = "Add Process Item";
      this.$root.$emit("bv::show::modal", this.infoModal.id);
    },
    deleteProcess(id) {
      this.$emit("delete-process", id);
    }
  }
};
</script>

<style scoped>
.pointer {
  cursor: pointer;
}
</style>
