<template>
  <div>
    <div class="row">
      <div class="col-md-8">
        <div class="row">
          <div class="col chart-bg p-3 m-3 overflow-auto">
            <h3 class="text-center pt-4">
              Process Heat Map
            </h3>
            <ScatterPlot :chart-data="scatterData"></ScatterPlot>
          </div>
        </div>
      </div>
      <div class="col-md-4">
        <div class="row">
          <div class="m-3 chart-bg col overflow-auto">
            <h6 class="text-center pt-3">Process Breakdown</h6>
            <DChart1 :chart-data="processDoughnutData" :height="190"></DChart1>
          </div>
        </div>
        <div class="row">
          <div class="m-3 chart-bg col overflow-auto">
            <h6 class="text-center pt-3">Opportunity Distribution</h6>
            <DChart2
              :chart-data="opportunityDoughnutData"
              :height="190"
            ></DChart2>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ScatterPlot from "./ScatterPlot";
import DChart1 from "./DoughnutChart1";
import DChart2 from "./DoughnutChart2";
export default {
  name: "GridLayout",
  components: { ScatterPlot, DChart1, DChart2 },
  props: ["processData"],
  watch: {
    processData() {
      this.fillScatterData();
      this.fillProcessDoughnutData();
      this.fillOpportunityDoughnutData();
    }
  },
  data() {
    return {
      scatterData: {},
      processDoughnutData: {},
      opportunityDoughnutData: {}
    };
  },
  mounted() {
    this.fillScatterData();
    this.fillProcessDoughnutData();
    this.fillOpportunityDoughnutData();
  },
  methods: {
    fillOpportunityDoughnutData() {
      var frontOfficeCount = 0;
      var backOfficeCount = 0;

      this.processData.forEach(function(process) {
        if (process.opportunity == "Front Office") {
          frontOfficeCount++;
        } else if (process.opportunity == "Back Office") {
          backOfficeCount++;
        }
      });
      this.opportunityDoughnutData = {
        labels: ["Front Office", "Back Office"],

        datasets: [
          {
            data: [frontOfficeCount, backOfficeCount],
            backgroundColor: [
              "rgba(254, 127, 156, 0.8)",
              "rgba(54, 162, 235, 0.8)"
            ]
          }
        ]
      };
    },
    fillProcessDoughnutData() {
      var greenCount = 0;
      var yellowCount = 0;
      var redCount = 0;
      this.processData.forEach(function(process) {
        if (process.recommendation == "Recommended") {
          greenCount++;
        } else if (process.recommendation == "Has Potential") {
          yellowCount++;
        } else if (process.recommendation == "Not Recommended") {
          redCount++;
        }
      });
      this.processDoughnutData = {
        labels: ["Recommended", "Has Potential", "Not Recommended"],

        datasets: [
          {
            data: [greenCount, yellowCount, redCount],
            backgroundColor: [
              "rgba(75, 192, 92, 0.8)",
              "rgba(255, 206, 86, 0.8)",
              "rgba(255, 99, 100, 0.8)"
            ]
          }
        ]
      };
    },
    fillScatterData() {
      var greenProcess = [];
      var yellowProcess = [];
      var redProcess = [];
      this.processData.forEach(function(process) {
        if (process.recommendation == "Recommended") {
          greenProcess.push({
            label: process.name,
            x: process.effort,
            y: process.savings
          });
        } else if (process.recommendation == "Has Potential") {
          yellowProcess.push({
            label: process.name,
            x: process.effort,
            y: process.savings
          });
        } else if (process.recommendation == "Not Recommended") {
          redProcess.push({
            label: process.name,
            x: process.effort,
            y: process.savings
          });
        }
      });
      this.scatterData = {
        datasets: [
          {
            label: "Recommended",
            backgroundColor: "rgba(75, 192, 92, 0.8)",
            borderWidth: "0px",
            radius: 5,
            data: greenProcess
          },
          {
            label: "Has Potential",
            backgroundColor: "rgba(255, 206, 86, 0.8)",
            borderWidth: "0px",
            radius: 5,
            data: yellowProcess
          },
          {
            label: "Not Recommended",
            backgroundColor: "rgba(255, 99, 100, 0.8)",
            borderWidth: "0px",
            radius: 5,
            data: redProcess
          }
        ]
      };
    }
  }
};
</script>

<style scoped>
.chart-bg {
  background-color: white;
  filter: drop-shadow(-3px 1px 4px grey);
}
</style>
