<template>
  <div id="app">
    <Banner v-bind:sidebarCollapsed="sidebarCollapsed" />

    <div class="container-fluid">
      <div class="row">
        <div
          id="sidebar"
          class="col-2 px-0 position-fixed light"
          v-show="!sidebarCollapsed"
        >
          <Sidebar />
        </div>
        <div
          id="content"
          v-bind:class="{
            'col-10': !sidebarCollapsed,
            'offset-2': !sidebarCollapsed,
            'col-12': sidebarCollapsed
          }"
          class="component-bg banner-height-correct"
        >
          <FourHighlights v-bind:processData="processData" />
          <ThreeGraphs v-bind:processData="processData" />
          <TableData
            v-on:add-process="addProcess"
            v-on:delete-process="deleteProcess"
            v-on:edit-process="editProcess"
            v-on:upload-csv="uploadCSV"
            v-on:download-csv="downloadCSV"
            :items="processData"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Banner from "./components/Banner.vue";
import Sidebar from "./components/Sidebar.vue";
import ThreeGraphs from "./components/ThreeGraphs.vue";
import FourHighlights from "./components/FourHighlights.vue";
import TableData from "./components/TableData.vue";

export default {
  name: "App",
  components: {
    Banner,
    Sidebar,
    ThreeGraphs,
    FourHighlights,
    TableData
  },
  data() {
    return {
      sidebarCollapsed: false,
      processData: [
        {
          id: 1,
          name: "Process 1",
          recommendation: "Recommended",
          opportunity: "Front Office",
          savings: 620,
          hours: 1200,
          effort: 2
        },
        {
          id: 2,
          name: "Process 2",
          recommendation: "Recommended",
          opportunity: "Back Office",
          savings: 845,
          hours: 1432,
          effort: 4
        },
        {
          id: 3,
          name: "Process 3",
          recommendation: "Has Potential",
          opportunity: "Back Office",
          savings: 570,
          hours: 770,
          effort: 6
        },
        {
          id: 4,
          name: "Process 4",
          recommendation: "Has Potential",
          opportunity: "Front Office",
          savings: 400,
          hours: 515,
          effort: 8
        },
        {
          id: 5,
          name: "Process 5",
          recommendation: "Not Recommended",
          opportunity: "Front Office",
          savings: 200,
          hours: 150,
          effort: 10
        },
        {
          id: 6,
          name: "Process 6",
          recommendation: "Not Recommended",
          opportunity: "Front Office",
          savings: 315,
          hours: 175,
          effort: 12
        }
      ]
    };
  },

  methods: {
    onResize() {
      if (window.innerWidth > 1200) {
        this.sidebarCollapsed = false;
      } else {
        this.sidebarCollapsed = true;
      }
    },
    addProcess(process) {
      this.processData.unshift({
        id: parseInt(process.id),
        name: process.name,
        recommendation: process.recommendation,
        opportunity: process.opportunity,
        savings: parseInt(process.savings),
        hours: parseInt(process.hours),
        effort: parseInt(process.effort)
      });
    },
    deleteProcess(id) {
      this.processData = this.processData.filter(item => item.id !== id);
    },
    editProcess(process) {
      var itemIndex = this.processData.findIndex(i => i.id === process.id);

      this.$set(this.processData, itemIndex, process);
    },
    dataToCSV(vueData) {
      let csvContent = "data:text/csv;charset=utf-8,";

      vueData.forEach(function(item) {
        let row = "";
        for (const key in item) {
          row += item[key] + ",";
        }
        //Remove extra comma
        row = row.substring(0, row.length - 1);
        csvContent += row + "\r\n";
      });
      csvContent = csvContent.trim();

      return csvContent;
    },

    downloadCSV() {
      let csvContent = this.dataToCSV(this.processData);

      let currentDate = new Date();

      let fileName = `${"" +
        currentDate.getMonth() +
        currentDate.getDate() +
        currentDate.getHours() +
        currentDate.getMinutes() +
        currentDate.getSeconds()}_Dashboard.csv`;

      var encodedUri = encodeURI(csvContent);
      var link = document.createElement("a");
      link.setAttribute("href", encodedUri);
      link.setAttribute("download", fileName);
      document.body.appendChild(link);

      link.click();
    },
    uploadCSV(file) {
      var reader = new FileReader();

      let vm = this;

      reader.onload = function(e) {
        let csvData = e.target.result;

        csvData = csvData.trim();

        let rows = csvData.split("\r\n");

        let formattedData = [];

        rows.forEach(function(row) {
          let processObj = {};
          let process = row.split(",");

          processObj.id = process[0];
          processObj.name = process[1];
          processObj.recommendation = process[2];
          processObj.opportunity = process[3];
          processObj.savings = parseInt(process[4]);
          processObj.hours = parseInt(process[5]);
          processObj.effort = parseInt(process[6]);

          formattedData.push(processObj);
        });

        vm.processData = formattedData;
      };
      reader.readAsText(file);
    }
  },

  created() {
    if (window.innerWidth > 1200) {
      this.sidebarCollapsed = false;
    } else {
      this.sidebarCollapsed = true;
    }
    window.addEventListener("resize", this.onResize);
  },

  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  }
};
</script>

<style>
#sidebar {
  min-height: 100vh;
  padding-top: 35px;
}

#content {
  padding-top: 65px;
  background-color: #f9f9f9;
}
</style>
